<template>
  <div class="home" :class="{ 'light-mode': isLightMode }">
    <h1>PPTËΩ¨ËßÜÈ¢ëÂ∑•ÂÖ∑</h1>
    <div class="container">
      <!-- Ê≠•È™§ÊåáÁ§∫Âô® -->
      <div class="steps">
        <div class="step" :class="{ active: currentStep >= 1 }">1. ‰∏ä‰º†PPT</div>
        <div class="step" :class="{ active: currentStep >= 2 }">2. ËÆæÁΩÆÂèÇÊï∞</div>
        <div class="step" :class="{ active: currentStep >= 3 }">3. ÁîüÊàêËßÜÈ¢ë</div>
      </div>
      
      <!-- Ê≠•È™§1ÔºöÊñá‰ª∂‰∏ä‰º† -->
      <div v-if="currentStep === 1" class="step-content">
        <!-- FileUploadÁªÑ‰ª∂ÂÜÖÂÆπ -->
        <div class="file-upload">
          <h2>‰∏ä‰º†PPTÊñá‰ª∂</h2>
          <div 
            class="upload-area" 
            :class="{ 'drag-over': isDragging }"
            @dragover.prevent="isDragging = true"
            @dragleave.prevent="isDragging = false"
            @drop.prevent="handleFileDrop"
          >
            <div v-if="!isUploading && !file">
              <i class="file-icon">üìÑ</i>
              <p>ÊãñÊîæPPTÊñá‰ª∂Âà∞Ê≠§Â§ÑÔºåÊàñ</p>
              <label class="upload-btn">
                ÈÄâÊã©Êñá‰ª∂
                <input type="file" accept=".ppt,.pptx" @change="handleFileChange" />
              </label>
              <p class="note">ÊîØÊåÅ.pptÂíå.pptxÊ†ºÂºèÔºåÊúÄÂ§ß50MB</p>
            </div>
            
            <div v-else-if="isUploading" class="uploading">
              <div class="spinner"></div>
              <p>Ê≠£Âú®‰∏ä‰º† {{ file.name }}</p>
              <p>{{ Math.round(uploadProgress) }}%</p>
            </div>
            
            <div v-else class="uploaded">
              <i class="file-icon">‚úÖ</i>
              <p>{{ file.name }}</p>
              <p>Â∑≤‰∏ä‰º†</p>
            </div>
          </div>
          
          <div v-if="error" class="error-message">
            {{ error }}
          </div>
        </div>
      </div>
      
      <!-- Ê≠•È™§2ÔºöÂèÇÊï∞ÈÖçÁΩÆ -->
      <div v-if="currentStep === 2" class="step-content">
        <!-- ConversionFormÁªÑ‰ª∂ÂÜÖÂÆπ -->
        <div class="conversion-form">
          <h2>ËÆæÁΩÆËΩ¨Êç¢ÂèÇÊï∞</h2>
          <p class="file-info">Êñá‰ª∂: {{ uploadedFilename }}</p>
          
          <form @submit.prevent="submitForm">
            <div class="form-group">
              <label>‰ΩøÁî®AIÁîüÊàêÊºîËÆ≤Á®ø</label>
              <div class="toggle-switch">
                <input 
                  type="checkbox" 
                  id="use-script" 
                  v-model="formData.use_generated_script"
                  @change="toggleScriptOptions"
                />
                <label for="use-script" class="toggle-label"></label>
              </div>
            </div>
            
            <!-- ÂΩìÈÄâÊã©‰ΩøÁî®AIÁîüÊàêÊºîËÆ≤Á®øÊó∂ÊòæÁ§∫ÁöÑÈÄâÈ°π -->
            <div v-if="formData.use_generated_script" class="script-options">
              <div class="form-group">
                <label for="script-style">ÊºîËÆ≤È£éÊ†º</label>
                <select id="script-style" v-model="formData.script_style">
                  <option value="Ê†áÂáÜ">Ê†áÂáÜ</option>
                  <option value="ÂπΩÈªò">ÂπΩÈªò</option>
                  <option value="‰∏•Ë∞®">‰∏•Ë∞®</option>
                  <option value="ÊøÄÂä±">ÊøÄÂä±</option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="target-audience">ÁõÆÊ†áÂèó‰ºó</label>
                <select id="target-audience" v-model="formData.target_audience">
                  <option value="ÈÄöÁî®">ÈÄöÁî®</option>
                  <option value="Â∞èÂ≠¶Áîü">Â∞èÂ≠¶Áîü</option>
                  <option value="Âàù‰∏≠Áîü">Âàù‰∏≠Áîü</option>
                  <option value="È´ò‰∏≠Áîü">È´ò‰∏≠Áîü</option>
                  <option value="Â§ßÂ≠¶Áîü">Â§ßÂ≠¶Áîü</option>
                  <option value="‰∏ì‰∏ö‰∫∫Â£´">‰∏ì‰∏ö‰∫∫Â£´</option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="total-duration">ÊºîËÆ≤Êó∂Èïø</label>
                <select id="total-duration" v-model="formData.total_duration">
                  <option value="ÁÆÄÁü≠">ÁÆÄÁü≠ (3-5ÂàÜÈíü)</option>
                  <option value="‰∏≠Á≠â">‰∏≠Á≠â (5-10ÂàÜÈíü)</option>
                  <option value="ËØ¶ÁªÜ">ËØ¶ÁªÜ (10-15ÂàÜÈíü)</option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="subject">Â≠¶ÁßëÁ±ªÂûã</label>
                <select id="subject" v-model="formData.subject">
                  <option value="ÈÄöÁî®">ÈÄöÁî®</option>
                  <option value="ËØ≠Êñá">ËØ≠Êñá</option>
                  <option value="Êï∞Â≠¶">Êï∞Â≠¶</option>
                  <option value="Ëã±ËØ≠">Ëã±ËØ≠</option>
                  <option value="Áâ©ÁêÜ">Áâ©ÁêÜ</option>
                  <option value="ÂåñÂ≠¶">ÂåñÂ≠¶</option>
                  <option value="ÁîüÁâ©">ÁîüÁâ©</option>
                  <option value="ÂéÜÂè≤">ÂéÜÂè≤</option>
                  <option value="Âú∞ÁêÜ">Âú∞ÁêÜ</option>
                  <option value="ÊîøÊ≤ª">ÊîøÊ≤ª</option>
                </select>
              </div>
            </div>
            
            <div class="form-group">
              <label for="slide-duration">ÈªòËÆ§ÂπªÁÅØÁâáÊó∂Èïø (Áßí)</label>
              <input 
                type="number" 
                id="slide-duration" 
                v-model.number="formData.slide_duration"
                min="1"
                max="30"
                step="1"
              />
              <span class="note">ÂΩìÂπªÁÅØÁâáÊ≤°ÊúâËØ≠Èü≥Êó∂ÁöÑÈªòËÆ§ÊòæÁ§∫Êó∂Èïø</span>
            </div>
            
            <div class="form-group">
              <label for="lang">ËØ≠Èü≥ËØ≠Ë®Ä</label>
              <select id="lang" v-model="formData.lang">
                <option value="zh-cn">‰∏≠Êñá (ÊôÆÈÄöËØù)</option>
                <option value="en-us">Ëã±ËØ≠ (ÁæéÂõΩ)</option>
              </select>
            </div>
            
            <!-- Êñ∞Â¢ûÔºöËØ≠ÈÄüÈÄâÊã© -->
            <div class="form-group">
              <label for="speed">ËØ≠ÈÄü</label>
              <div class="slider-container">
                <input 
                  type="range" 
                  id="speed" 
                  v-model.number="formData.speed" 
                  min="0" 
                  max="100" 
                  step="1" 
                  class="slider"
                />
                <span class="slider-value">{{ formData.speed }}</span>
              </div>
              <span class="note">0-ÊÖ¢ÈÄüÔºå50-Ê≠£Â∏∏Ôºå100-Âø´ÈÄü</span>
            </div>
            
            <!-- Êñ∞Â¢ûÔºöÂèëÈü≥‰∫∫ÈÄâÊã© -->
            <div class="form-group">
              <label for="vcn">ÂèëÈü≥‰∫∫</label>
              <select id="vcn" v-model="formData.vcn">
                <option value="x4_mingge">ÊòéÂì• (Áî∑Â£∞)</option>
                <option value="x4_xiaoying">ÁßÄËã± (ËÄÅÂπ¥Â•≥Â£∞)</option>
                <option value="x4_xiaoguo">Â∞èÊûú (Â•≥Â£∞)</option>
                <option value="x4_xiaozhong">Â∞èÂø† (Áî∑Â£∞)</option>
                <option value="x4_qianxue">ÂçÉÈõ™ (Â•≥Â£∞)</option>
                <option value="x4_yeting">Â∏åÊ∂µ (Â•≥Â£∞)</option>
                <option value="x4_lingbosong">ËÅÜ‰ºØÊùæ (ËÄÅÂπ¥Áî∑Â£∞)</option>
                <option value="x4_lingxiaoshan_profnews">ËÅÜÂ∞èÁèä (Â•≥Â£∞)</option>
                <option value="x4_doudou">Ë±ÜË±Ü (Áî∑Á´•Â£∞)</option>
                <option value="x4_chaoge">Ë∂ÖÂì• (Áî∑Â£∞)</option>
                <option value="x4_guanyijie">ÂÖ≥Â±± (‰∏ìÈ¢òÁî∑Â£∞)</option>
                <option value="x4_feidie">È£ûÁ¢üÂì• (Áî∑Â£∞)</option>
              </select>
            </div>
            
            <div class="form-actions">
              <button type="submit" class="btn-submit">ÂºÄÂßãËΩ¨Êç¢</button>
            </div>
          </form>
          <button class="btn-secondary" @click="currentStep = 1">ËøîÂõû</button>
        </div>
      </div>
      
      <!-- Ê≠•È™§3ÔºöÂ§ÑÁêÜ‰∏≠ -->
      <div v-if="currentStep === 3" class="step-content">
        <div class="processing">
          <h2>Ê≠£Âú®Â§ÑÁêÜÊÇ®ÁöÑPPT...</h2>
          <div class="progress-bar">
            <div class="progress-animation"></div>
          </div>
          <p>ËØ∑ËÄêÂøÉÁ≠âÂæÖÔºåËΩ¨Êç¢ËøáÁ®ãÂèØËÉΩÈúÄË¶ÅÂá†ÂàÜÈíü„ÄÇ</p>
          <p>Â§ÑÁêÜÁä∂ÊÄÅ: {{ processingStatus }}</p>
        </div>
      </div>
      
      <!-- Ê≠•È™§4ÔºöÁªìÊûú -->
      <div v-if="currentStep === 4" class="step-content">
        <div v-if="conversionSuccess" class="success">
          <h2>ËΩ¨Êç¢ÊàêÂäüÔºÅ</h2>
          <!-- VideoPlayerÁªÑ‰ª∂ÂÜÖÂÆπ -->
          <div class="video-player">
            <video
              ref="videoPlayer"
              class="video-js"
              controls
              preload="auto"
              width="100%"
              height="auto"
              :src="videoUrl"
              data-setup="{}"
              @error="handleVideoError"
            >
              <p class="vjs-no-js">
                Ë¶ÅÊü•ÁúãÊ≠§ËßÜÈ¢ëÔºåËØ∑ÂêØÁî®JavaScriptÂπ∂ËÄÉËôëÂçáÁ∫ßÂà∞ÊîØÊåÅHTML5ËßÜÈ¢ëÁöÑÊµèËßàÂô®
              </p>
            </video>
            <div v-if="videoError" class="error-message">
              ËßÜÈ¢ëÂä†ËΩΩÈîôËØØ: {{ videoError }}
            </div>
          </div>
          <div class="actions">
            <button class="btn-primary" @click="downloadVideo">‰∏ãËΩΩËßÜÈ¢ë</button>
            <button class="btn-secondary" @click="resetForm">ÂºÄÂßãÊñ∞ÁöÑËΩ¨Êç¢</button>
          </div>
        </div>
        <div v-else class="error">
          <h2>ËΩ¨Êç¢Â§±Ë¥•</h2>
          <p>{{ errorMessage }}</p>
          <button class="btn-secondary" @click="resetForm">ÈáçËØï</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

// ÂàõÂª∫Ëá™ÂÆö‰πâÁöÑaxiosÂÆû‰æãÔºåÊõø‰ª£vue.config.js‰∏≠ÁöÑ‰ª£ÁêÜÈÖçÁΩÆ
const apiClient = axios.create({
  baseURL: 'http://localhost:2200/pptv',
  headers: {
    'Content-Type': 'application/json'
  }
});

export default {
  name: 'CombinedComponent',
  data() {
    return {
      // Ê∑ªÂä†Â§úÈó¥Ê®°ÂºèÂ±ûÊÄß
      isLightMode: localStorage.getItem("theme-mode") === "light",
      
      // HomeÁªÑ‰ª∂Êï∞ÊçÆ
      currentStep: 1,
      taskId: null,
      uploadedFilename: '',
      processingStatus: 'ÂàùÂßãÂåñ‰∏≠...',
      statusInterval: null,
      conversionSuccess: false,
      videoUrl: '',
      errorMessage: '',
      videoError: null,
      
      // FileUploadÁªÑ‰ª∂Êï∞ÊçÆ
      file: null,
      isDragging: false,
      isUploading: false,
      uploadProgress: 0,
      error: null,
      
      // ConversionFormÁªÑ‰ª∂Êï∞ÊçÆ
      formData: {
        use_generated_script: true,
        script_style: 'Ê†áÂáÜ',
        target_audience: 'ÈÄöÁî®',
        total_duration: '‰∏≠Á≠â',
        subject: 'ÈÄöÁî®',
        slide_duration: 5,
        lang: 'zh-cn',
        // Êñ∞Â¢ûÂèÇÊï∞
        speed: 50,
        vcn: 'x4_mingge'
      }
    };
  },
  mounted() {
    // Ê∑ªÂä†‰∏ªÈ¢òÁõëÂê¨
    this.checkThemeMode();
    window.addEventListener("storage", this.checkThemeMode);
    window.addEventListener("themeChange", this.checkThemeMode);
  },
  beforeUnmount() {
    // Ê∏ÖÁêÜinterval
    if (this.statusInterval) {
      clearInterval(this.statusInterval);
    }
    
    // ÁßªÈô§‰∏ªÈ¢òÁõëÂê¨
    window.removeEventListener("storage", this.checkThemeMode);
    window.removeEventListener("themeChange", this.checkThemeMode);
  },
  methods: {
    // Ê∑ªÂä†‰∏ªÈ¢òÊ£ÄÊµãÊñπÊ≥ï
    checkThemeMode() {
      this.isLightMode = localStorage.getItem("theme-mode") === "light";
    },
    
    // ‰∏ãËΩΩËßÜÈ¢ëÊñπÊ≥ï
    downloadVideo() {
      if (!this.videoUrl) return;
      
      // ÂàõÂª∫‰∏Ä‰∏™‰∏¥Êó∂ÁöÑÈöêËóèaÊ†áÁ≠æ
      const downloadLink = document.createElement('a');
      downloadLink.href = `${this.videoUrl}?download=true`;
      downloadLink.setAttribute('download', `${this.uploadedFilename.split('.')[0]}.mp4`);
      downloadLink.setAttribute('target', '_blank');
      
      // Ê∑ªÂä†Âà∞ÊñáÊ°£‰∏≠Âπ∂Ëß¶ÂèëÁÇπÂáª
      document.body.appendChild(downloadLink);
      downloadLink.click();
      
      // ÁßªÈô§‰∏¥Êó∂ÂÖÉÁ¥†
      setTimeout(() => {
        document.body.removeChild(downloadLink);
      }, 100);
    },
    
    // FileUploadÁªÑ‰ª∂ÊñπÊ≥ï
    handleFileChange(event) {
      const selectedFile = event.target.files[0];
      if (selectedFile) {
        this.validateAndSetFile(selectedFile);
      }
    },
    handleFileDrop(event) {
      this.isDragging = false;
      const droppedFile = event.dataTransfer.files[0];
      if (droppedFile) {
        this.validateAndSetFile(droppedFile);
      }
    },
    validateAndSetFile(file) {
      // Ê£ÄÊü•Êñá‰ª∂Á±ªÂûã
      const validTypes = ['application/vnd.ms-powerpoint', 'application/vnd.openxmlformats-officedocument.presentationml.presentation'];
      const fileExtension = file.name.split('.').pop().toLowerCase();
      
      if (!validTypes.includes(file.type) && !['ppt', 'pptx'].includes(fileExtension)) {
        this.error = 'Âè™ÊîØÊåÅPPTÊñá‰ª∂(.ppt Êàñ .pptx)';
        return;
      }
      
      // Ê£ÄÊü•Êñá‰ª∂Â§ßÂ∞è (50MBÈôêÂà∂)
      if (file.size > 50 * 1024 * 1024) {
        this.error = 'Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá50MB';
        return;
      }
      
      this.file = file;
      this.error = null;
      this.uploadFile();
    },
    uploadFile() {
      this.isUploading = true;
      this.uploadProgress = 0;
      
      const formData = new FormData();
      formData.append('file', this.file);
      
      apiClient.post('/api/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: progressEvent => {
          this.uploadProgress = (progressEvent.loaded / progressEvent.total) * 100;
        }
      })
      .then(response => {
        this.isUploading = false;
        // Â§ÑÁêÜ‰∏ä‰º†ÊàêÂäü
        this.handleFileUploaded({
          task_id: response.data.task_id,
          filename: this.file.name
        });
      })
      .catch(error => {
        this.isUploading = false;
        this.error = error.response?.data?.error || '‰∏ä‰º†Â§±Ë¥•ÔºåËØ∑ÈáçËØï';
        this.file = null;
      });
    },
    
    // ConversionFormÁªÑ‰ª∂ÊñπÊ≥ï
    toggleScriptOptions() {
      // ÂΩìÂàáÊç¢AIÁîüÊàêÈÄâÈ°πÊó∂ÔºåÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†È¢ùÂ§ñÈÄªËæë
    },
    submitForm() {
      this.startConversion(this.formData);
    },
    
    // HomeÁªÑ‰ª∂ÊñπÊ≥ï
    handleFileUploaded(response) {
      this.taskId = response.task_id;
      this.uploadedFilename = response.filename;
      this.currentStep = 2;
    },
    startConversion(params) {
      this.currentStep = 3;
      
      // ÂèëÈÄÅËΩ¨Êç¢ËØ∑Ê±Ç
      apiClient.post(`/api/convert/${this.taskId}`, params)
        .then(() => {
          // ÂêØÂä®Áä∂ÊÄÅËΩÆËØ¢
          this.checkTaskStatus();
        })
        .catch(error => {
          this.handleError(error);
        });
    },
    checkTaskStatus() {
      // Ê∏ÖÈô§‰πãÂâçÁöÑinterval
      if (this.statusInterval) {
        clearInterval(this.statusInterval);
      }
      
      // ËÆæÁΩÆÊñ∞ÁöÑËΩÆËØ¢
      this.statusInterval = setInterval(() => {
        apiClient.get(`/api/tasks/${this.taskId}`)
          .then(response => {
            this.processingStatus = response.data.status;
            
            if (response.data.status === 'completed') {
              clearInterval(this.statusInterval);
              this.conversionSuccess = true;
              // ‰øÆÊîπÔºöÁ°Æ‰øùÊó†ËÆ∫URLÊ†ºÂºèÂ¶Ç‰ΩïÈÉΩËÉΩÊ≠£Á°ÆÂ§ÑÁêÜ
              let videoUrl = response.data.video_url;
              if (videoUrl) {
                if (!videoUrl.startsWith('http') && !videoUrl.startsWith('/')) {
                  // Ê∑ªÂä†ÊñúÊù†‰ª•Á°Æ‰øùURLÊ≠£Á°ÆÊãºÊé•
                  this.videoUrl = `${apiClient.defaults.baseURL}/${videoUrl}`;
                } else if (!videoUrl.startsWith('http') && videoUrl.startsWith('/')) {
                  // Â∑≤ÊúâÊñúÊù†ÁöÑÊÉÖÂÜµ
                  this.videoUrl = `${apiClient.defaults.baseURL}${videoUrl}`;
                } else {
                  this.videoUrl = videoUrl;
                }
              }
              this.currentStep = 4;
            } else if (response.data.status === 'failed') {
              clearInterval(this.statusInterval);
              this.conversionSuccess = false;
              this.errorMessage = response.data.error || 'ËΩ¨Êç¢ËøáÁ®ã‰∏≠Âá∫Áé∞ÈîôËØØ';
              this.currentStep = 4;
            }
          })
          .catch(error => {
            clearInterval(this.statusInterval);
            this.handleError(error);
          });
      }, 3000); // ÊØè3ÁßíÊ£ÄÊü•‰∏ÄÊ¨°
    },
    handleError(error) {
      this.conversionSuccess = false;
      this.errorMessage = error.response?.data?.error || 'ÂèëÁîüÊú™Áü•ÈîôËØØ';
      this.currentStep = 4;
    },
    resetForm() {
      this.currentStep = 1;
      this.taskId = null;
      this.uploadedFilename = '';
      this.processingStatus = 'ÂàùÂßãÂåñ‰∏≠...';
      this.conversionSuccess = false;
      this.videoUrl = '';
      this.errorMessage = '';
      this.file = null;
      this.isDragging = false;
      this.isUploading = false;
      this.uploadProgress = 0;
      this.error = null;
      this.formData = {
        use_generated_script: true,
        script_style: 'Ê†áÂáÜ',
        target_audience: 'ÈÄöÁî®',
        total_duration: '‰∏≠Á≠â',
        subject: 'ÈÄöÁî®',
        slide_duration: 5,
        lang: 'zh-cn',
        // Êñ∞Â¢ûÂèÇÊï∞
        speed: 50,
        vcn: 'x4_mingge'
      };
      
      if (this.statusInterval) {
        clearInterval(this.statusInterval);
        this.statusInterval = null;
      }
    },
    // Êñ∞Â¢ûÔºöÂ§ÑÁêÜËßÜÈ¢ëÈîôËØØ
    handleVideoError(e) {
      this.videoError = `ËßÜÈ¢ëÊó†Ê≥ïÊí≠Êîæ (ÈîôËØØ‰ª£Á†Å: ${e.target.error ? e.target.error.code : 'Êú™Áü•'})`;
      console.error('ËßÜÈ¢ëÂä†ËΩΩÈîôËØØ:', e);
    }
  }
};
</script>

<style scoped>
/* HomeÁªÑ‰ª∂Ê†∑Âºè - Â§úÈó¥Ê®°Âºè(ÈªòËÆ§) */
.home {
  width: 100%;
  height: 100%;
  padding: 20px;
  overflow-y: auto;
